import { ParameterControllerPropsWithTree } from '@engine/controller';
import { requestGet, requestPost } from '@engine/crud';

/**
 * 获取数据字典的基础方法
 * @param widgetName 控件名称
 * @param op 其余参数
 */
function fetchCellBasicControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): Promise<any> {
    return requestPost('cell/widget/data', {
        data: {
            ...op,
            widgetname: widgetName,
        },
    });
}


/**
 * 下拉框控件获取数据字典
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchCellComboControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): Promise<{
        text: string;
        value: any;
    }[]> {
    return fetchCellBasicControlAttr(widgetName, op);
}

/**
 * 请求cell view value的最基础请求
 * @param params params
 */
export function fetchCellWidgetViewValue(params: {[key:string]: any }): Promise<{ value: { val: string | number, text: string | number } } | string | {}> {
    return requestGet('cell/widget/view/value', {
        params,
    });
}

/**
 * 日期控件获取数据字典
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchCellDatetimeControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): Promise<{
        startDate: string;
        endDate: string;
    }[]> {
    return fetchCellBasicControlAttr(widgetName, op);
}

/**
 * 按钮组控件获取数据字典
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchCellGroupControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): Promise<{
        text: string;
        value: any;
    }[]> {
    return fetchCellBasicControlAttr(widgetName, op);
}

/**
 * 老树控件获取数据字典
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchOldCellTreeControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): ReturnType<ParameterControllerPropsWithTree['oldTreeControlAttrFetcher']> {
    return fetchCellBasicControlAttr(widgetName, op);
}

/**
 * 新树控件获取单个节点下的树形结构
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchNewCellTreeStructure(
    widgetName: string,
    op: {[key: string]: any} = {},
): ReturnType<ParameterControllerPropsWithTree['newTreeStructureFetcher']> {
    return fetchCellBasicControlAttr(widgetName, {
        ...op,
        fine_outlet_widget: 'nx',
        completion: true,
    });
}

/**
 * 获取新树控件返回值
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchNewCellTreeControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): ReturnType<ParameterControllerPropsWithTree['newTreeControlAttrFetcher']> {
    return fetchCellBasicControlAttr(widgetName, {
        ...op,
        fine_outlet_widget: 'nx',
    });
}

/**
 * 网页框控件获取数据字典
 * @param widgetName 控件名称
 * @param op 其余参数
 */
export function fetchIframeControlAttr(
    widgetName: string,
    op: {[key: string]: any} = {},
): Promise<{
        [key: string]: string;
    }[]> {
    return fetchCellBasicControlAttr(widgetName, op);
}
